*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

:root {
  --gold: hsl(24, 56%, 63%);
  --black: hsl(0, 0%, 8%);
  --gray: hsl(0, 0%, 27%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-shoulder: "Big Shoulders Display", cursive;
  --font-outfit: "Outfit", sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
  font-family: var(--font-outfit);
}
@media (min-width: 64em) {
  body {
    font-size: 2.2rem;
  }
}

.heading-primary {
  font-size: 6rem;
  font-family: var(--font-shoulder);
  text-transform: uppercase;
  line-height: 0.92;
}
@media (min-width: 36em) {
  .heading-primary {
    font-size: 7rem;
  }
}
@media (min-width: 64em) {
  .heading-primary {
    position: relative;
    font-size: 9rem;
  }
}
.heading-primary span {
  display: block;
}
@media (min-width: 64em) {
  .heading-primary .modern {
    position: relative;
    white-space: nowrap;
  }
  .heading-primary .modern::before {
    content: attr(data-split);
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    width: 49%;
    overflow: hidden;
  }
}
@media (min-width: 64em) {
  .heading-primary .art-gallery {
    position: relative;
    white-space: nowrap;
  }
  .heading-primary .art-gallery::before {
    content: attr(data-split);
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    width: 49%;
    overflow: hidden;
  }
}

.heading-secondary {
  font-size: 5rem;
  font-family: var(--font-shoulder);
  text-transform: uppercase;
  line-height: 0.92;
}
.heading-secondary span {
  display: block;
}

.heading-tertiary {
  font-size: 3.6rem;
  font-family: var(--font-shoulder);
  text-transform: uppercase;
  line-height: 0.92;
}
.heading-tertiary span {
  display: block;
}

.container {
  margin-left: 1.6rem;
  margin-right: 1.6rem;
}
@media (min-width: 36em) {
  .container {
    margin-left: 6rem;
    margin-right: 6rem;
  }
}
@media (min-width: 48em) {
  .container {
    max-width: 68.9rem;
    margin: 0 auto;
  }
}
@media (min-width: 64em) {
  .container {
    max-width: 96rem;
  }
}
@media (min-width: 75em) {
  .container {
    max-width: 110rem;
  }
}

.button {
  border: none;
  display: flex;
  height: 7.2rem;
  cursor: pointer;
}
.button span {
  height: 100%;
}
.button-text {
  width: 20.4rem;
  padding: 2.1rem 0;
  font-family: var(--font-shoulder);
  text-transform: uppercase;
  letter-spacing: 3.64px;
  color: var(--white);
  background-color: var(--black);
  transition: 0.2s;
}
.button-img {
  width: 5.6rem;
  padding: 2.4rem;
  background-color: var(--gold);
  transition: 0.2s;
}
.button:hover .button-text {
  background-color: var(--gold);
}
.button:hover .button-img {
  background-color: var(--black);
}

.hero {
  position: relative;
  margin-bottom: 12rem;
}
.hero__img {
  background-color: var(--black);
  width: 100%;
}
@media (min-width: 48em) {
  .hero__img {
    width: 43.7rem;
  }
}
@media (min-width: 64em) {
  .hero__img {
    width: 90rem;
  }
}
@media (min-width: 75em) {
  .hero__img {
    width: 99rem;
  }
}
.hero__img-inner {
  width: 100%;
}
@media (min-width: 48em) {
  .hero__img-inner {
    width: auto;
  }
}
@media (min-width: 64em) {
  .hero__img-inner {
    margin-left: auto;
  }
}
.hero__content {
  margin-top: 3.2rem;
}
@media (min-width: 48em) {
  .hero__content {
    width: 34rem;
    position: absolute;
    margin: 0 4rem;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
}
@media (min-width: 60em) {
  .hero__content {
    margin: 0 9rem;
  }
}
@media (min-width: 64em) {
  .hero__content {
    display: flex;
    width: auto;
    margin: 0;
    top: 30%;
    right: 50%;
    transform: translateX(50%);
  }
}
@media (min-width: 75em) {
  .hero__content {
    column-gap: 18rem;
  }
}
.hero__title {
  margin-bottom: 3.2rem;
}
@media (min-width: 48em) {
  .hero__title {
    margin-bottom: 4.8rem;
  }
}
@media (min-width: 64em) {
  .hero__title {
    width: 51.4rem;
  }
}
@media (min-width: 64em) {
  .hero__inner {
    width: 35rem;
  }
}
@media (min-width: 75em) {
  .hero__inner {
    padding-left: 6rem;
  }
}
.hero__description {
  margin-bottom: 3.2rem;
}
@media (min-width: 48em) {
  .hero__description {
    margin-bottom: 4.8rem;
  }
}

.gallery {
  margin-bottom: 12rem;
}
.gallery__first {
  display: flex;
  flex-direction: column;
  row-gap: 2.4rem;
}
@media (min-width: 48em) {
  .gallery__first {
    flex-direction: row-reverse;
    row-gap: 0;
    column-gap: 6.8rem;
    margin-bottom: 1.1rem;
  }
}
@media (min-width: 64em) {
  .gallery__first {
    column-gap: 12.5rem;
  }
}
@media (min-width: 75em) {
  .gallery__first {
    margin-bottom: 3rem;
  }
}
.gallery__img {
  flex: 39.8rem;
}
@media (min-width: 64em) {
  .gallery__img {
    flex: 63.5rem;
  }
}
.gallery__img img {
  width: 100%;
}
.gallery__text {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  margin-bottom: 3.2rem;
}
@media (min-width: 48em) {
  .gallery__text {
    flex: 22.3rem;
  }
}
@media (min-width: 64em) {
  .gallery__text {
    flex: 35rem;
  }
}
.gallery__second {
  display: grid;
  width: 100%;
  overflow: hidden;
  row-gap: 1.6rem;
}
@media (min-width: 48em) {
  .gallery__second {
    grid-template-columns: 39.8rem 28rem;
    grid-template-rows: 31.3rem 39.6rem;
    grid-template-areas: "a b" "a c";
    column-gap: 1.1rem;
    row-gap: 1.1rem;
  }
}
@media (min-width: 64em) {
  .gallery__second {
    grid-template-columns: 1.42fr 1fr;
    grid-template-rows: 31.3rem 37.7rem;
  }
}
@media (min-width: 75em) {
  .gallery__second {
    grid-template-columns: 63.5rem 44.5rem;
    column-gap: 3rem;
    row-gap: 3rem;
  }
}
@media (min-width: 48em) {
  .gallery__img-2 {
    grid-area: a;
  }
}
@media (min-width: 64em) {
  .gallery__img-2 {
    height: 70.1rem;
  }
}
@media (min-width: 75em) {
  .gallery__img-2 {
    width: 100%;
    height: 72rem;
  }
}
@media (min-width: 48em) {
  .gallery__img-3 {
    grid-area: b;
  }
}
@media (min-width: 64em) {
  .gallery__img-3 {
    height: 100%;
  }
}
.gallery__inspired {
  background-color: var(--black);
  padding: 4.8rem 2.4rem;
  color: var(--white);
  display: flex;
  flex-direction: column;
  row-gap: 2.4rem;
}
@media (min-width: 48em) {
  .gallery__inspired {
    grid-area: c;
  }
}

.footer {
  background-color: var(--black);
  color: var(--white);
  padding: 4.8rem 3.2rem;
  display: flex;
  flex-direction: column;
  row-gap: 3.8rem;
}
.footer__sns {
  display: flex;
  column-gap: 2rem;
}
.footer__sns-content {
  color: var(--white);
  transition: 0.2s;
  cursor: pointer;
}
.footer__sns-content:hover {
  color: var(--gold);
}
.footer__sns-content svg {
  fill: currentColor;
}
.footer__sns-icon {
  width: 2rem;
  height: 2rem;
}